document.querySelectorAll(".media").forEach((block) => {
  const buttons = block.querySelectorAll(".filter-btn");
  const cards = block.querySelectorAll(".media__card");
  const modal = document.getElementById("mediaModal");
  const modalBody = modal.querySelector(".media-modal__body");
  const closeBtn = modal.querySelector(".media-modal__close");

  // фильтрация
  buttons.forEach((btn) => {
    btn.addEventListener("click", () => {
      buttons.forEach((b) => b.classList.remove("active"));
      btn.classList.add("active");

      const filter = btn.dataset.filter;
      cards.forEach((card) => {
        card.style.display =
          filter === "all" || card.dataset.category === filter ? "block" : "none";
      });
    });
  });

  // открытие
  cards.forEach((card) => {
    card.addEventListener("click", () => {
      const type = card.dataset.type;
      const src = card.dataset.src;
      modalBody.innerHTML = "";

      if (type === "image") {
        const img = document.createElement("img");
        img.src = src;
        modalBody.appendChild(img);
      } else if (type === "video") {
        const iframe = document.createElement("iframe");
        iframe.src = src + "?autoplay=1&rel=0";
        iframe.allow =
          "accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture";
        iframe.allowFullscreen = true;
        modalBody.appendChild(iframe);
      }

      modal.classList.add("active");
    });
  });

  closeBtn.addEventListener("click", closeModal);
  modal.addEventListener("click", (e) => {
    if (e.target === modal) closeModal();
  });

  function closeModal() {
    modal.classList.remove("active");
    setTimeout(() => (modalBody.innerHTML = ""), 300);
  }
});



