(function () {
  const input = document.getElementById('files-upload');
  const list = document.querySelector('.contacts__upload-list');

  if (!input || !list) return;

  input.addEventListener('change', function () {
    list.innerHTML = '';
    const files = Array.from(input.files || []);
    if (files.length === 0) {
      list.textContent = '';
      return;
    }

    files.forEach(f => {
      const el = document.createElement('span');
      el.className = 'contacts__upload-file';
      // обрезаем длинные имена
      const name = f.name.length > 30 ? f.name.slice(0, 28) + '…' : f.name;
      el.textContent = name;
      list.appendChild(el);
    });
  });

  // keyboard accessibility: нажать Enter/Space на label тоже откроет диалог (по умолчанию работает),
  // если нужно — можно добавить обработчик клавиш.
})();







document.addEventListener("DOMContentLoaded", function () {
  document.querySelectorAll("[data-select]").forEach(select => {
    const trigger = select.querySelector("[data-select-trigger]");
    const options = select.querySelector(".form__custom-options");
    const hiddenInput = select.querySelector("input[type='hidden']");

    trigger.addEventListener("click", () => {
      select.classList.toggle("open");
      trigger.classList.toggle("active");
    });

    options.querySelectorAll("li").forEach(option => {
      option.addEventListener("click", () => {
        trigger.textContent = option.textContent;
        hiddenInput.value = option.dataset.value;
        select.classList.remove("open");
        trigger.classList.remove("active");
      });
    });

    document.addEventListener("click", e => {
      if (!select.contains(e.target)) {
        select.classList.remove("open");
        trigger.classList.remove("active");
      }
    });
  });
});




document.addEventListener('DOMContentLoaded', () => {
  const burger = document.querySelector('[data-burger]');
  const menu = document.querySelector('[data-menu]');
  const links = document.querySelectorAll('.header__menu-link');

  if (!burger || !menu) return;

  burger.addEventListener('click', () => {
    burger.classList.toggle('active');
    menu.classList.toggle('menu--active');

    const isOpen = burger.classList.contains('active');
    burger.setAttribute('aria-expanded', isOpen ? 'true' : 'false');
  });

  links.forEach(link => {
    link.addEventListener('click', () => {
      burger.classList.remove('active');
      menu.classList.remove('menu--active');
      burger.setAttribute('aria-expanded', 'false');
    });
  });
});



